body,html {
    overflow-x: hidden;

    .background_controller {
        position: absolute;
        width: 450px;
        height: auto;
        z-index: -1;

        &:first-of-type {
            top: 1000px;
            left: 0;
        }

        &:last-of-type {
            top: 1500px;
            right: 0;
        }
    }
}